* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}

.heading_style {
  padding: 55px 0;
  text-align: center;
  font-family: 'Libre Baskerville', serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 600;
  color: black;
  background-color: rgb(12, 173, 173);
  font-size: 2.5rem;
  margin-bottom: 30px;

}

.cards {
  width: 100%;
  height: auto;
}

.card {
  box-sizing: border-box;
  margin: 6%;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
  background-color: #fff;
  width: 20%;
  border-radius: 12px;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
  float: left;
}

.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.2, 1.2);
}

.card_img {
  width: 100%;
  height: 320px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.card_info {
  background-color: #fff;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}

.card_category {
  font-family: 'Libre Baskerville', serif;
  /* text-transform: uppercase; */
  font-size: 13px;
  letter-spacing: 2px;
  color: #868686;
  background-color: #fff;
}

.card_title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: 'Libre Baskerville', serif;
  background-color: #fff;
  text-transform: capitalize;
  font-size:25px;
}

button {
  padding: 5px 8px;
  font-family: 'Libre Baskerville', serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 1px;
  font-weight: 550;
  color: black;
  background-color: #ebcdf9;
  outline: none;
  border: 1px solid rgb(249, 244, 244);
  cursor: pointer;
  border-radius: 5px;
}
@media screen and (max-width: 400px), screen and (max-width: 500px), screen and (max-width: 600px)
, screen and (max-width: 768px) {
  .card {
    width:87%;
  }
}